import React from 'react';
import * as myConstants from '../constants';
import { stringify } from 'query-string';

export {ChineseCharSelectable};


class ChineseCharSelector extends React.Component {
    constructor (props) {
        super(props);
        this.state = {
            my_char: ""
        }
        this.inputUpdated = this.inputUpdated.bind(this);
        this.submitChar = this.submitChar.bind(this);
    }
    submitChar(event) {
        event.preventDefault();
        const { my_char } = this.state;
        const { onSubmit } = this.props
        onSubmit(my_char);
    }
    inputUpdated(event) {
        this.setState({ my_char: event.target.value });
    }
    render() {
        return (
            <form onSubmit={this.submitChar}>
                <h3>{this.props.title ? this.props.title : "Chinese Character Selector"}</h3>
                <label htmlFor="asdf">chinses?? </label>
                <input name="chinese character input" type="text" step="any" value={this.state.my_char} onInput={this.inputUpdated} />
                <button type="submit" className='btn btn-success'>Submit</button>
            </form>
        )
    }
}

 class ChineseCharSelectable extends React.Component {
    constructor(props) {
        super(props);
        this.state = {char: "", isLoaded: "asdfaaa"};
        this.onFormSubmit = this.onFormSubmit.bind(this);
    }

    // unPackJsonResult(json_result) {
    //     for (var key in props.char_dir) {
    //         this.state.char = key;
    //         this.state.pos_info = props.char_dir[key][0];
    //         this.state.sub_chars = props.char_dir[key][1];
    //     }
    // }


    onFormSubmit(chinese_char) {
        // var url = new URL(myConstants.CHINESE_CHAR_TREE_ENDPOINT);
        // var params = {char: chinese_char};
        // url.search = new URLSearchParams(params).toString();
        // console.log(url)
        const url = myConstants.CHINESE_CHAR_TREE_ENDPOINT +"?" + stringify({char: chinese_char});
        console.log(url);
        this.setState({url: url});
        fetch(url)
        .then(res => res.json())
        .then(
          (result) => {
            this.setState({
              isLoaded: true,
              json_result: result,
            }); console.log("Result: "); console.log(result);
          },
          // Note: it's important to handle errors here
          // instead of a catch() block so that we don't swallow
          // exceptions from actual bugs in components.
          (error) => {
            this.setState({
              isLoaded: true,
              error
            });
          }
        )
        this.setState({ char: chinese_char});
    }

    render() {
        return (
            <div>
                <ChineseCharSelector onSubmit={this.onFormSubmit} />
                <h2 style={{color: 'black'}}>{this.state.char}</h2>
                <h2 style={{color: 'black'}}>isLoaded: {this.state.isLoaded}</h2>
                <h2 style={{color: 'black'}}>json_result: {this.state.json_result ? this.state.json_result[Object.keys(this.state.json_result)[0]][0]: "whatever"}</h2>
                <h3>This should be my ChineseCharDisplayeraaa</h3>
                <ChineseCharDisplayerMain char_dir={this.state.json_result} key={this.state.char == null ? null: this.state.char}/>
            </div>
        )
    }
}

class ChineseCharDisplayerMain extends React.Component {
    render() {
        
        return (
            <ChineseCharDisplayer char={this.props.char_dir == null ? "N/A": Object.keys(this.props.char_dir)[0]} />
        )
    }
}

class ChineseCharDisplayer extends React.Component {
    render() {
        return (
            <div>
                <h2 style={{color: 'black'}}>{this.props.char}</h2>
            </div>
        )
    }
}
